            /*
             * Text Animations
             *
             * @param  {Boolean} init      - Initialize load e.
             * @param  {Boolean} loopAnim  - Looping animation.
             * @return {Void}
             */ 
			function animeForText( init, loopAnim ) {
                
                
                const initFn = function() {
                    const $slides = $( mainTextDescSliderID );

                    $slides.each( function( index )  {
                        const $this   = $( this ),
                              textArr = [ $this.find( '> h2' ), $this.find( '> p' ) ];

                        textArr.forEach((el, index) => {
                            const titleText = new SplitText( el, { type: "chars" } );
                            TweenMax.set( titleText.chars, { 
                                y: 70, 
                                alpha: 0 
                            } );		
                        });// end forEach         
                        
                    });    
                };
                

                
                //Initialize the text display.
                if ( init ) {

                    initFn();

                } else {

                    const $slides = $( mainTextDescSliderID );
                    let currentSlide = 0;
                    let textAnim = true;


                    //display text wrapper
                    $( mainTextSliderID ).addClass( 'is-active' );


                    const nextSlide = function() {	
                        
                        textAnim = true;
                        

                        //
                        if ( textAnim ) {
                            const $this   = $slides.eq( currentSlide ),
                                  textArr = [ $this.find( '> h2' ), $this.find( '> p' ) ];
                            
                            textAnim = false;
                            
                            textArr.forEach((el, index) => {


                                const titleText = new SplitText( el, { type: "chars" } );
                                const numTitleChars = titleText.chars.length;
                                const tl = new TimelineMax({
                                    repeat: 0,
                                    onComplete: function() {
                                        //tl.pause("explode");
                                    }
                                });
                                

                                ////randomly change alpha of each word
                                for (let i = 0; i < numTitleChars; i++) {
                                    tl.from( titleText.chars[i], 1, {
                                        opacity: 0,
                                        webkitFilter: "blur(0)",
                                        ease: Back.easeOut,
                                    }, Math.random() * 1 );
                                } 


                                
                                //add explode label 2 seconds after reset animation is done
                                tl.add("explode", "+=2");

                                //add explode effect
                                for (let i = 0; i < numTitleChars; i++) {
                                    tl.to( titleText.chars[i], 0.5, {
                                        delay: 1.5,
                                        opacity: 0,
                                        webkitFilter: "blur(5px)",
                                    },"explode+=" + Math.random() * 0.2);
                                }
       
                                
                                /*
                                tl.staggerFrom( titleText.chars, 1.5, {
                                    ease: Back.easeOut,
                                    y: 120,
                                    alpha: 0
                                }, 0.02 )
                                .staggerTo( titleText.chars, 1.5, {
                                    delay: 2,
                                    y: -50,
                                    scaleY: 1.2,
                                    ease: Back.easeOut,
                                    alpha: 0
                                }, 0.03, "chars+=0.1" );
                                */


                            });// end forEach  

                        

                            //
                            if ( $( mainTextSliderID ).hasClass( 'is-click' ) || !loopAnim ) {
                                
                                //kill only delayedCalls
                                TweenMax.killAll(false, false, true, false);
                                
                                setTimeout( function() {
                                    initFn();
                                }, 1000 );
                            } else {
                                TweenMax.delayedCall( $( mainTextSliderID ).data( 'tween-delay' ), nextSlide );
                            }
                        

                            
                        }//endif textAnim

                        //slide animation
                        if (currentSlide < $slides.length - 1 ) {
                            currentSlide++;
                        } else {
                            currentSlide = 0;
                        }

                        

                    };// end nextSlide()

                    nextSlide();


                }//endif init

                   
              
            }
            
            

